
<template>
<div class="m-2 bg-white border rounded" id='snapshot'>
    <div class="row justify-content-center align-items-center" >

        <table class="table">
            <tr>
                <th v-for='heading in headings' :key="heading" scope="col">{{heading}}</th>
            </tr>

            <tr v-for="(row, index) in getHistory" :id="'history-' + index" :key="index">
                <td v-for='key in Object.keys(row)' :key="key">{{row[key]}}</td>
            </tr>

            <tr :class='getLiveClass' id="current-data" key="current-data">
                <td v-for='key in Object.keys(getCurrent)' :key="key">{{getCurrent[key]}}</td>
            </tr>
                            
        </table> 

	</div>


    <div class='d-grid gap-2 d-sm-block'>
        <button id="snapshot-button" type='button' class="btn btn-sm me-2" @click="takeSnapshot">Record Snapshot</button>
        <button id="reset-snaps" type='button' class="btn btn-sm me-2" @click="toggleResetModal">Reset</button>
        <button id="download-snaps" type='button' class="btn btn-sm me-2" @click="outputToCSV">Download Snapshots</button>
    </div>
    
     <toolbar parentCanvasID="" parentComponentName="snapshot" parentDivID="motor-snapshot-div" :showDownload='false' :showPopupHelp="true" :showOptions="false">  
        <template v-slot:popup id='snapshot-popup'>
            <div class='row mb-2'>
                <div class='col'>
                    <h3> Snapshot tool </h3>
                    <p> Click 'Record Snapshot' to save the current data. Every time you click a new data set will be added. Click 'Download Snapshots'
                        to download all the snapshots as a .csv file
                    </p>
                </div>
            </div>

        </template>
    </toolbar>

    <div v-if='showResetConfirmModal' class="modal modal-show" tabindex="-1" role="dialog">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Reset Data</h5>
              <button type="button" class="btn btn-close" data-bs-dismiss="modal" aria-label="Close" @click='toggleResetModal'>
                
              </button>
            </div>
            <div class="modal-body">
              <p>Are you sure you want to Reset? This will clear all stored data.</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" @click="resetSnaps(); toggleResetModal();">Reset</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal" data-bs-dismiss="modal" @click="toggleResetModal">Cancel</button>
            </div>
          </div>
        </div>
      </div>

</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Toolbar from "./elements/Toolbar.vue"

export default {
    name: 'Snapshot',
    props:[
      'headings'
    ],
    components:{
        Toolbar,
    },
    data(){
        return{
            showResetConfirmModal: false
        }
    },
    computed:{
        ...mapGetters([
            'getCurrent',
            'getHistory',
            'getSmState'
        ]),
        getLiveClass(){
            if(this.getSmState == 1){
                return 'current border'
            } else{
                return ''
            }
        }
        
    },
    methods: {
        takeSnapshot(){
            let data = this.getCurrent;
            this.$store.dispatch('addToHistory', data);
        },
        resetSnaps(){
            this.$store.dispatch('clearAllHistory');
        },
        toggleResetModal(){
            this.showResetConfirmModal = !this.showResetConfirmModal;
        },
        outputToCSV(){
          let csv = '';
          let filename = '';
            let date = new Date();
            filename = 'TRUSS_SNAPSHOTS_' + date.getDate().toString() + (date.getMonth() + 1).toString() + date.getFullYear().toString();
          
            
              csv = 'Load/N, Gauge 1/ , Gauge 2/,Gauge 3/, Gauge 4/, Gauge 5/, Gauge 6/\n';
            this.getHistory.forEach(function(d){
                csv += d.load_cell.toString();
                csv += ",";
                csv += d.gauge_1.toString();
                csv += ',';
                csv += d.gauge_2.toString();
                csv += ',';
                csv += d.gauge_3.toString();
                csv += ',';
                csv += d.gauge_4.toString();
                csv += ',';
                csv += d.gauge_5.toString();
                csv += ',';
                csv += d.gauge_6.toString();
                     
                csv += "\n";
            });
            filename += '.csv';
          
          let hiddenElement = document.createElement('a');
          hiddenElement.href = 'data:text/csv;charset=utf-8,' + encodeURI(csv);
          hiddenElement.target = '_blank';
          hiddenElement.download = filename;
          hiddenElement.click();
      },
      },
      
}
</script>

<style scoped>
#snapshot-button        {background-color: rgb(217, 255, 0);}
#snapshot-button:hover  {background-color: rgb(190, 187, 2);}
#reset-snaps        {background-color: rgb(243, 117, 44);}
#reset-snaps:hover  {background-color: rgb(243, 7, 7);}
#download-snaps        {background-color: rgb(44, 243, 200);}
#download-snaps:hover  {background-color: rgb(7, 82, 243);}


